<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>土壤信息管理</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:soliIM:add')">新增</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:soliIM:modify')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:solilM:delete')">删除</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:solilM:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                </div>
            </div>
            <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
            <div v-show="inforadmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                    <div class="form-group">
                        <label class="control-label col-sm-2">土壤类型名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('soil_type_name')&&addData.soil_type_name!='','has-error':errors.has('soil_type_name')}">
                            <input type="text" class="form-control" name="soil_type_name" :placeholder="'请输入类型名'" v-model="addData.soil_type_name" :data-vv-as="'类型名'" v-validate="$api.opreate.validateAdd('name',50,stateid,opreate,'Sensor')">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('soil_type_name')">{{errors.first("soil_type_name")}}</span>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">备注</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('remark')&&addData.remark!='','has-error':errors.has('remark')}">
                            <textarea rows="6" class="form-control" name="remark" :placeholder="'请输入备注'" v-model="addData.remark" :data-vv-as="'备注'" v-validate="'required'"></textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('remark')">{{errors.first("remark")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>
土壤信息管理
<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            inforadmin: false,
            opreate: "add",
            tableSelectData: {
                selectTableurl: "agriculture/selectSoilTypeInfo.action",
                pageloginData: {
                    id: this.stateid
                },
                filterName: []
            },
            addData: {
                soil_type_id:"",
                soil_type_name:"",
                remark:""
            }
        }
    },
    props: {
        stateid: String,
        statetitle: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        }
    },
    components: {
        tables,
    },
    methods: {
        //添加按钮按下
        inforAddClick(bool) {
            var vm=this;
            var opreate=this.opreate;
            if(opreate=='update'){
                this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]);
            }
            async function Empty(){
                   for (var key in vm.addData) {
                    vm.addData[key] = "";
                    }
                    return true;
            }
         Empty().then(() => {
                vm.opreate = "add";
                vm.inforadmin = bool;
                vm.$validator.errors.items=[]; //将表单错误数组重置为0
            })
        },
        
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck(vm, "修改")) {
                this.inforadmin = true;
                this.opreate = "update";
                this.addData =this.$api.opreate.ObjectCopy(this.checkData[0]);
            }
        },
        //添加函数
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData;
            var datas = {
                data: data,
                states: {
                    id: stateId
                }
            };
            // 修改操作
           
            
            async function addDataFun(){
             var bool;
            if (vm.opreate == "update") {
               bool = vm.$api.http.opreateHttp(vm, "agriculture/updateSoilTypeInfo.action", datas, "修改", "");
            } else if (vm.opreate == "add") {
                //添加操作
                 bool = vm.$api.http.opreateHttp(vm, "agriculture/addSoilTypeInfo.action", datas, "添加", "");
            }
            return bool;
            }
            addDataFun().then(function (success) {
                
                        vm.inforAddClick(false);
                    
            })
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
            if (vm.$api.opreate.TESTcheck(vm, "删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].soil_type_id,
                            states: {
                                id: vm.stateid
                            }
                        };
                        console.log(checkData[0]);
                        
                        vm.$api.http.opreateHttp(
                            vm,
                            "agriculture/deleteSoilTypeInfo.action",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入场景类型名后搜索", {
                    icon: 0
                });
            }
        }
    }
};
</script>

<style>

</style>



